.react-responsive-modal-modal {
    max-width: 800px;
    display: inline-block;
    text-align: left;
    vertical-align: middle;
    background: #fff;
    margin: 1.2rem;
    padding: 1.2rem;
    position: relative;
    overflow-y: auto;
    border-radius: 10px;
}
.react-responsive-modal-closeButton {
    position: absolute;
    top: 50px;
    right: 30px;
    border: none;
    padding: 0;
    cursor: pointer;
    background-color: transparent;
    display: flex;
}
.model_styles {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2em;
    h2 {
        font: var(--unnamed-font-style-normal) normal
            var(--unnamed-font-weight-bold) var(--unnamed-font-size-45) /
            var(--unnamed-line-spacing-55)
            var(--unnamed-font-family-cormorant-garamond);
        letter-spacing: var(--unnamed-character-spacing--0-45);
        color: var(--unnamed-color-161616);
        text-align: center;
        font: normal normal bold 36px/55px Cormorant Garamond;
        letter-spacing: -0.45px;
        color: #161616;
        opacity: 1;
    }
    p {
        font: var(--unnamed-font-style-normal) normal
            var(--unnamed-font-weight-normal) var(--unnamed-font-size-18) /
            var(--unnamed-line-spacing-32) var(--unnamed-font-family-montserrat);
        letter-spacing: var(--unnamed-character-spacing-0-27);
        color: var(--unnamed-color-2c2c2c);
        text-align: center;
        font: normal normal normal 18px/32px Montserrat;
        letter-spacing: 0.27px;
        color: #2c2c2c;
        opacity: 1;
    }
}
